.Contact{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 100%;
    width: 50%;
    text-align: left;
}
form{
    display: flex;
    flex-direction: column;
    height: 70%;
    justify-content: space-between;
    gap:10px;
    width: 100%;
}
.hdtxt{
    margin-bottom: 5%;
    font-size: 1rem;
}
label{
    display: flex;
    flex-direction: column;
    margin-bottom: .4rem;
    color: #344054;
    font-size: .8rem;
    font-weight: 500;
}
.frmname{
    display: flex;
    flex-direction: row;
    gap: 5%;
}
.frmname label{
    width: 50%;
}

::placeholder {
    color: #667085;
    opacity: 1; /* Firefox */
    font-size: .8rem;
  }
  
  :-ms-input-placeholder { /* Internet Explorer 10-11 */
   color: #667085;
   font-size: .8rem;
  }
  
  ::-ms-input-placeholder { /* Microsoft Edge */
   color: #667085;
   font-size: .8rem;
  }
input:hover{
    border: 1px solid #84CAFF;
}
textarea{
    height: 6rem;
    border-radius: 5px;
    margin-top: .6rem;
    border: 1px solid #D0D5DD;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    padding: 1%;
    box-sizing: border-box;
    color: #667085;
    resize: none;
}
textarea:hover{
    box-shadow: 0px 0px 0px 4px #D1E9FF;
    border: 1px solid #84CAFF;
}
.checkl{
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.check{
    width: 2%;
    height: 1rem;
    margin: 0;
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}
.checkmark {
    height: 15px;
    width: 15px;
    border-radius: 3px;
    background-color: white;
    border: 1px solid #D0D5DD;
  }
  /* On mouse-over, add a grey background color */
.checkl:hover input ~ .checkmark {
    box-shadow: 0px 0px 0px 4px #D1E9FF;
    border: 1px solid #84CAFF;
  }
  
  /* Create the checkmark/indicator (hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  .checkl input:checked ~ .checkmark {
    border: 1px solid #1570EF;
  }
  /* Show the checkmark when checked */
  .checkl input:checked ~ .checkmark:after {
    display: block;
  }
  
  /* Style the checkmark/indicator */
  .checkl .checkmark:after {
    width: 5px;
    height: 8px;
    margin-left: 3.5px;
    border: solid #1570EF;
    border-width: 0 2.5px 2.5px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
.chktxt{
    margin-left: 2%;
}
.submit{
    height: 2.5rem;
    margin-bottom: 5rem;
    border: none;
    background-color: #175CD3;
    color: white;
    margin-top: 0;
    font-size: .8rem;
}
.submit:hover{
    background-color: #175cd36f;
}
h2{
    font-size: 2rem;
    margin: .5rem 0;
    font-weight: 600;
}
.error{
    color: red;
    margin-top: .3rem;
    font-size: .8rem;
    display: none;
}

