.App {
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.lnktree{
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: 70%;
  width: 70%;
  padding-bottom: 3%;
}
.link{
  text-decoration: none;
  color: black;
  background-color: rgba(140, 146, 174, 0.267);
  height: 50%;
  width: 100%;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2.5% 0;
  cursor: pointer;
  flex-direction: column;
  transition: .1s;
}
.link:hover{
  background-color: #8c92ae6d;
}
footer{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  width: 80%;
  height: 10%;
}
hr{
  height: .5px;
  width: 80%;
  background-color: rgba(128, 128, 128, 0.622);
  outline: none;
  border: none;
}
#profile_img{
  height: 5.4rem;
  width: 6.5%;
  object-fit: cover;
  border-radius: 50%;
}
.name{
  padding: 2%;
  font-size: 1.2rem;
  font-weight: 700;
}
.zuri{
  font-size: 1.3rem;
  font-weight: 700;
  display: flex;
  flex-direction: row;
  align-items: baseline;
}
.dot{
  font-size: 2.5rem;
  color: red;
}
#logo{
  width: 13%;
}
.imglnk{
  height: 2rem;
  width: 7%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-bottom: 4%;
}
.tlnk{
  height: 100%;
}
.share{
  border: 2px dotted rgba(140, 146, 174, 0.267);
  padding: 5px;
  border-radius: 50%;
  float: right;
  width: 5%;
}
.share:hover{
  background-color: rgba(140, 146, 174, 0.267);
  cursor: pointer;
}
.mft{
  font-size: 1rem;
}
.sbtxt{
  background-color: white;
  border-radius: 5px;
  padding: 5px;
  margin-top: 5px;
  display: none;
  transition: 0.3ms;
  font-size: .9rem;
  font-style: italic;
  font-weight: 450;
  transition: 1s;
}

.shrbtn {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  gap: 0;
  width: 30%;
}

.shrbtn .shrtxt {
  visibility: hidden;
  width: 80px;
  background-color: #101828;
  color: #fff;
  font-size: .8rem;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  top: 4px;
  left: 70%;
  opacity: 0;
  transition: opacity 0.3s;
  cursor: pointer;
}

.shrbtn .shrtxt::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 100%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #101828 transparent transparent transparent;
}

.shrbtn:hover .shrtxt {
  visibility: visible;
  opacity: 1;
}



@media only screen and (max-width: 1198px){
  #profile_img{
    height: 5rem;
    width: 6.8%;
  }
  .shrbtn .shrtxt {
    left: 72%;
  }
  .shrbtn {
    width: 40%;
  }
  .imglnk{
    width: 7%;
  }
}
